/* http://meyerweb.com/eric/tools/css/reset/
   v2.0-modified | 20110126
   License: none (public domain)
*/
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700&display=swap");
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* make sure to set some focus styles for accessibility */
:focus {
  outline: 0;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  -webkit-appearance: none;
  -moz-appearance: none;
}

input[type=search] {
  -webkit-appearance: none;
  -moz-appearance: none;
  -webkit-box-sizing: content-box;
  -moz-box-sizing: content-box;
  box-sizing: content-box;
}

textarea {
  overflow: auto;
  vertical-align: top;
  resize: vertical;
}

/**
 * Correct `inline-block` display not defined in IE 6/7/8/9 and Firefox 3.
 */
audio,
canvas,
video {
  display: inline-block;
  *display: inline;
  *zoom: 1;
  max-width: 100%;
}

/**
 * Prevent modern browsers from displaying `audio` without controls.
 * Remove excess height in iOS 5 devices.
 */
audio:not([controls]) {
  display: none;
  height: 0;
}

/**
 * Address styling not present in IE 7/8/9, Firefox 3, and Safari 4.
 * Known issue: no IE 6 support.
 */
[hidden] {
  display: none;
}

/**
 * 1. Correct text resizing oddly in IE 6/7 when body `font-size` is set using
 *    `em` units.
 * 2. Prevent iOS text size adjust after orientation change, without disabling
 *    user zoom.
 */
html {
  font-size: 100%; /* 1 */
  -webkit-text-size-adjust: 100%; /* 2 */
  -ms-text-size-adjust: 100%; /* 2 */
}

/**
 * Address `outline` inconsistency between Chrome and other browsers.
 */
a:focus {
  outline: thin dotted;
}

/**
 * Improve readability when focused and also mouse hovered in all browsers.
 */
a:active,
a:hover {
  outline: 0;
}

/**
 * 1. Remove border when inside `a` element in IE 6/7/8/9 and Firefox 3.
 * 2. Improve image quality when scaled in IE 7.
 */
img {
  border: 0; /* 1 */
  -ms-interpolation-mode: bicubic; /* 2 */
}

/**
 * Address margin not present in IE 6/7/8/9, Safari 5, and Opera 11.
 */
figure {
  margin: 0;
}

/**
 * Correct margin displayed oddly in IE 6/7.
 */
form {
  margin: 0;
}

/**
 * Define consistent border, margin, and padding.
 */
fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}

/**
 * 1. Correct color not being inherited in IE 6/7/8/9.
 * 2. Correct text not wrapping in Firefox 3.
 * 3. Correct alignment displayed oddly in IE 6/7.
 */
legend {
  border: 0; /* 1 */
  padding: 0;
  white-space: normal; /* 2 */
  *margin-left: -7px; /* 3 */
}

/**
 * 1. Correct font size not being inherited in all browsers.
 * 2. Address margins set differently in IE 6/7, Firefox 3+, Safari 5,
 *    and Chrome.
 * 3. Improve appearance and consistency in all browsers.
 */
button,
input,
select,
textarea {
  font-size: 100%; /* 1 */
  margin: 0; /* 2 */
  vertical-align: baseline; /* 3 */
  *vertical-align: middle; /* 3 */
}

/**
 * Address Firefox 3+ setting `line-height` on `input` using `!important` in
 * the UA stylesheet.
 */
button,
input {
  line-height: normal;
}

/**
 * Address inconsistent `text-transform` inheritance for `button` and `select`.
 * All other form control elements do not inherit `text-transform` values.
 * Correct `button` style inheritance in Chrome, Safari 5+, and IE 6+.
 * Correct `select` style inheritance in Firefox 4+ and Opera.
 */
button,
select {
  text-transform: none;
}

/**
 * 1. Avoid the WebKit bug in Android 4.0.* where (2) destroys native `audio`
 *    and `video` controls.
 * 2. Correct inability to style clickable `input` types in iOS.
 * 3. Improve usability and consistency of cursor style between image-type
 *    `input` and others.
 * 4. Remove inner spacing in IE 7 without affecting normal text inputs.
 *    Known issue: inner spacing remains in IE 6.
 */
button,
html input[type=button],
input[type=reset],
input[type=submit] {
  -webkit-appearance: button; /* 2 */
  cursor: pointer; /* 3 */
  *overflow: visible; /* 4 */
}

/**
 * Re-set default cursor for disabled elements.
 */
button[disabled],
html input[disabled] {
  cursor: default;
}

/**
 * 1. Address box sizing set to content-box in IE 8/9.
 * 2. Remove excess padding in IE 8/9.
 * 3. Remove excess padding in IE 7.
 *    Known issue: excess padding remains in IE 6.
 */
input[type=checkbox],
input[type=radio] {
  box-sizing: border-box; /* 1 */
  padding: 0; /* 2 */
  *height: 13px; /* 3 */
  *width: 13px; /* 3 */
}

/**
 * 1. Address `appearance` set to `searchfield` in Safari 5 and Chrome.
 * 2. Address `box-sizing` set to `border-box` in Safari 5 and Chrome
 *    (include `-moz` to future-proof).
 */
input[type=search] {
  -webkit-appearance: textfield; /* 1 */
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box; /* 2 */
  box-sizing: content-box;
}

/**
 * Remove inner padding and search cancel button in Safari 5 and Chrome
 * on OS X.
 */
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

/**
 * Remove inner padding and border in Firefox 3+.
 */
button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

/**
 * 1. Remove default vertical scrollbar in IE 6/7/8/9.
 * 2. Improve readability and alignment in all browsers.
 */
textarea {
  overflow: auto; /* 1 */
  vertical-align: top; /* 2 */
}

/**
 * Remove most spacing between table cells.
 */
table {
  border-collapse: collapse;
  border-spacing: 0;
}

html,
button,
input,
select,
textarea {
  color: #222;
}

::-moz-selection {
  background: #b3d4fc;
  text-shadow: none;
}

::selection {
  background: #b3d4fc;
  text-shadow: none;
}

img {
  vertical-align: middle;
}

fieldset {
  border: 0;
  margin: 0;
  padding: 0;
}

textarea {
  resize: vertical;
}

.chromeframe {
  margin: 0.2em 0;
  background: #ccc;
  color: #000;
  padding: 0.2em 0;
}

.scroll-to-top-btn, body button.secondary, body button.primary, body a.secondary, body a.primary {
  padding: 1rem 2rem;
  margin: 2rem 0;
  border-radius: 0.25rem;
  font-weight: 500;
  font-size: 1rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  line-height: 140%;
  text-align: center;
  cursor: pointer;
}

@font-face {
  font-family: "Tahu";
  src: url("../fonts/tahu-webfont.woff2") format("woff2"), url("../fonts/tahu-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: "Montserrat", sans-serif;
  max-width: 100vw;
  overflow-x: hidden;
  background-color: #ecf9fc;
}
body h1 {
  font-size: 2.25rem;
  font-weight: 400;
  color: rgb(40, 40, 40);
  line-height: 150%;
  letter-spacing: 0.5px;
  margin: 2rem 0 0.5rem 0;
  max-width: 800px;
  margin-bottom: 5rem;
  position: relative;
}
@media screen and (min-width: 900px) {
  body h1 {
    margin-bottom: 10rem;
  }
}
body h1 span.decor {
  display: block;
  font-family: "Tahu";
  position: absolute;
  font-weight: 400;
  font-size: 6rem;
  left: 4rem;
}
@media screen and (min-width: 900px) {
  body h1 span.decor {
    font-size: 10rem;
  }
}
body h2 {
  font-size: 1.25rem;
  font-weight: 600;
  color: rgb(40, 40, 40);
  line-height: 150%;
  letter-spacing: 0.5px;
  margin: 0.5rem 0;
  margin-bottom: 1.5rem;
  max-width: 600px;
}
body h3 {
  font-size: 1.25rem;
  font-weight: 500;
  color: rgb(40, 40, 40);
  line-height: 150%;
  letter-spacing: 0.5px;
  margin: 1rem 0;
  max-width: 600px;
}
body h4 {
  font-size: 1.25rem;
  font-weight: 500;
  color: rgb(40, 40, 40);
  line-height: 150%;
  letter-spacing: 0.5px;
  margin: 2rem 0;
}
body p {
  font-size: 1rem;
  font-weight: 400;
  color: rgb(40, 40, 40);
  line-height: 150%;
  letter-spacing: 0.5px;
  margin: 1rem 0;
  max-width: 600px;
}
body p span {
  display: inline-block;
}
@media screen and (min-width: 900px) {
  body h1 {
    font-size: 3rem;
  }
  body h2,
  body h3,
  body h4 {
    font-size: 1.5rem;
  }
}
body a {
  font-family: "Montserrat", sans-serif;
  text-decoration: none;
  color: inherit;
}
body a.primary {
  display: inline-block;
  display: flex;
  justify-content: center;
  background-color: #6EC846;
  border: none;
  color: white;
  border: 1px solid #6EC846;
}
@media screen and (min-width: 500px) {
  body a.primary {
    min-width: 286px;
  }
}
body a.primary:hover {
  background-color: #4BC8E6;
  border-color: #4BC8E6;
}
body a.secondary {
  display: inline-block;
  background-color: transparent;
  border: 1px solid #6EC846;
  color: #6EC846;
}
@media screen and (min-width: 500px) {
  body a.secondary {
    min-width: 286px;
  }
}
body a.secondary:hover {
  background-color: rgba(255, 255, 255, 0.25);
}
body button.primary {
  background-color: #6EC846;
  border: none;
  color: white;
  font-family: "Montserrat", sans-serif;
}
@media screen and (min-width: 500px) {
  body button.primary {
    min-width: 286px;
  }
}
body button.primary:hover {
  background-color: #4BC8E6;
}
body button.secondary {
  background-color: transparent;
  border: 1px solid #6EC846;
  color: #6EC846;
  font-family: "Montserrat", sans-serif;
  padding: 0.5rem 1rem;
}
body button.secondary:hover {
  background-color: rgba(255, 255, 255, 0.25);
}
body header > h1 {
  display: none;
}
body .container {
  width: 90%;
  margin: 0 auto;
}
@media screen and (min-width: 900px) {
  body .container {
    width: 85%;
  }
}

.d-none {
  display: none;
}

.background-image {
  display: none;
  height: 100vh;
  width: 100%;
  background-image: url("../assets/hero.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: fixed;
  right: -200px;
  z-index: -1;
}
@media screen and (min-width: 1200px) {
  .background-image {
    display: block;
  }
}

.mobile-background-image {
  height: 100vw;
  width: 100%;
  overflow-x: hidden;
  background-image: url("../assets/heroMobile.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
@media screen and (min-width: 1200px) {
  .mobile-background-image {
    display: none;
  }
}

img.logo {
  width: 100%;
  max-width: 200px;
  margin: 2rem 0;
}
@media screen and (min-width: 900px) {
  img.logo {
    margin: 5rem 0 2rem 0;
  }
}

.icon {
  margin-right: 0.5rem;
}

.hero {
  min-height: 100vh;
}
.hero .hero-content a {
  margin: 0;
}
.hero .hero-content > div:last-child {
  padding: 1.5rem 0;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}
@media screen and (min-width: 900px) {
  .hero .hero-content > div:last-child {
    flex-direction: row;
  }
}

.form-section {
  min-height: 100vh;
  padding: 5rem 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: white;
}
.form-section div {
  max-width: 600px;
}

footer {
  background-color: #4BC8E6;
  min-height: 50vh;
  color: white;
  position: relative;
  padding: 5rem 0;
}
footer h4,
footer address,
footer p {
  color: white;
}
footer .social-icons {
  margin-top: 5rem;
}
@media screen and (min-width: 500px) {
  footer .social-icons {
    text-align: right;
    margin-top: 0;
  }
}
footer .social-icons .social-icon {
  margin: 1rem;
}

.scroll-to-top-btn {
  display: none;
  background-color: transparent;
  border: 1px solid rgb(40, 40, 40);
  color: rgb(40, 40, 40);
  font-family: "Montserrat", sans-serif;
  position: fixed;
  bottom: 5rem;
  right: 5%;
  width: 3rem;
  height: 3rem;
  z-index: 99;
  margin: 0;
  padding: 0;
}
.scroll-to-top-btn div {
  width: 1rem;
  height: 1rem;
  border-top: 1px solid rgb(40, 40, 40);
  border-left: 1px solid rgb(40, 40, 40);
  transform: rotate(45deg);
  position: relative;
  left: 14px;
  top: 4px;
}
@media screen and (min-width: 1200px) {
  .scroll-to-top-btn {
    display: block;
    right: 1.5rem;
  }
}

.cookies {
  width: 100%;
  position: fixed;
  bottom: 0;
  background-color: rgb(40, 40, 40);
  z-index: 10;
  padding: 0 5%;
}
@media screen and (min-width: 900px) {
  .cookies {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
  }
}
.cookies button.secondary {
  margin: 1rem 0;
}
.cookies p {
  font-size: 0.8rem;
  font-weight: 300;
  color: white;
  line-height: 150%;
  letter-spacing: 0.5px;
  max-width: 90%;
}